// Initialisation principale
Office.onReady(() => {
  // Cache toutes les interfaces au dpart
  hideAllScreens();

  // Affiche l'cran de bienvenue
  showScreen("welcome");

  // Configure les vnements
  setupEventListeners();

});

function hideAllScreens() {
  // Ces IDs doivent correspondre  ton HTML
  const screens = ["welcome-screen", "main-menu", "drilling-interface","export-instructions"];
  screens.forEach((id) => {
    const element = document.getElementById(id);
    if (element) element.style.display = "none";
  });
}

function showScreen(screenName) {
  hideAllScreens();

  appState.currentScreen = screenName;

  switch (screenName) {
    case "welcome":
      document.getElementById("welcome-screen").style.display = "flex";
      break;
    case "menu":
      resetView();
      document.getElementById("main-menu").style.display = "flex";
      break;
    case "drilling":
      document.getElementById("drilling-interface").style.display = "block";
      // Initialise le canvas si ce n'est pas dj fait
      if (!isInitialized) {
        initEmptyCanvas();
      }
      break;
    case "export-instructions": 
      document.getElementById("export-instructions").style.display = "flex";
      break;
  }

}

