async function resetProjetComplet() {
    const confirmBox = document.getElementById("confirm-reset");
    const resetBtn = document.getElementById("reset-project-btn");
    const originalBtnText = resetBtn.textContent;
    const originalBtnClass = resetBtn.className;
    const originalBtnColor = resetBtn.style.backgroundColor;
    // AJOUT: Variable pour suivre l'tat de verrouillage de Feuil1
    let feuilleFeuil1Deverrouillee = false;
    
    try {
        confirmBox.style.display = "none";
        
        resetBtn.disabled = true;
        resetBtn.classList.add("disabled");
        resetBtn.style.backgroundColor = "#cccccc";
        resetBtn.textContent = "Rinitialisation en cours...";
        
        await Excel.run(async (context) => {
            const sheets = context.workbook.worksheets;
            
            const sheetsToClear = {
                "ACCUEIL": ["B13:I150"],
                "Feuil1": ["A37:B170", "F37:J170"]
            };
            
            // AJOUT: Dbut du bloc de gestion de la protection de Feuil1
            // Charger l'tat de protection
            const feuille1 = sheets.getItem("Feuil1");
            feuille1.load("protection/protected");
            await context.sync();
            
            // Vrifier si la feuille est protge
            if (feuille1.protection.protected) {
                // Dverrouiller avec le mot de passe
                await setSheetProtection("Feuil1", false, "Gp4r!74.");
                feuilleFeuil1Deverrouillee = true; // Marquer comme dverrouille
            }
            // FIN AJOUT
            
            for (const sheetName in sheetsToClear) {
                if (sheetsToClear.hasOwnProperty(sheetName)) {
                    const sheet = sheets.getItem(sheetName);
                    for (const range of sheetsToClear[sheetName]) {
                        sheet.getRange(range).clear(Excel.ClearApplyTo.all);
                    }
                }
            }
            
            await context.sync();
        });
        
        // AJOUT: Reverrouiller Feuil1 aprs succs des oprations
        if (feuilleFeuil1Deverrouillee) {
            await setSheetProtection("Feuil1", true, "Gp4r!74.");
            feuilleFeuil1Deverrouillee = false;
        }
        
        resetBtn.classList.remove("disabled");
        resetBtn.classList.add("success");
        resetBtn.style.backgroundColor = "#28a745";
        resetBtn.innerHTML = " Rinitialisation russie";
        
        setTimeout(() => {
            resetBtn.disabled = false;
            resetBtn.style.backgroundColor = originalBtnColor;
            resetBtn.className = originalBtnClass;
            resetBtn.textContent = originalBtnText;
        }, 3000);
        
    } catch (error) {
        console.error("chec de la rinitialisation:", error);
        
        // AJOUT: Bloc de gestion d'erreur pour reverrouiller Feuil1 si ncessaire
        if (feuilleFeuil1Deverrouillee) {
            try {
                await setSheetProtection("Feuil1", true, "Gp4r!74.");
            } catch (verrouillageError) {
                console.error("Erreur lors du reverrouillage d'urgence de Feuil1:", verrouillageError);
            }
        }
        
        resetBtn.classList.remove("disabled");
        resetBtn.classList.add("error");
        resetBtn.style.backgroundColor = "#dc3545"
        resetBtn.innerHTML = " chec de la rinitialisation";
        resetBtn.disabled = false;
        
        setTimeout(() => {
            resetBtn.style.backgroundColor = originalBtnColor;
            resetBtn.className = originalBtnClass;
            resetBtn.textContent = originalBtnText;
        }, 3000);
    }
}

