async function retourAccueilEtMasquerAutresFeuilles() {
  try {
    await Excel.run(async (context) => {
      const sheets = context.workbook.worksheets;
      sheets.load("items/name,items/visibility");
      await context.sync();

      // Vrifie si ACCUEIL est masque et l'affiche si ncessaire
      const accueilSheet = sheets.getItem("ACCUEIL");
      accueilSheet.load("visibility");
      await context.sync();

      if (accueilSheet.visibility !== "Visible") {
        accueilSheet.visibility = "Visible";
        await context.sync();
      }

      // Active ACCUEIL
      accueilSheet.activate();
      await context.sync();

      // Masquer toutes les autres feuilles visibles
      for (const sheet of sheets.items) {
        if (sheet.name !== "ACCUEIL" && sheet.visibility === "Visible") {
          sheet.visibility = "Hidden";
        }
      }

      await context.sync();
    });

    // Affiche le menu (interface HTML/JS)
    showScreen("menu");

  } catch (error) {
    console.error("Erreur lors du retour  ACCUEIL :", error);
    showError(`Erreur : ${error.message}`);
  }
}


async function unhideSheetsExport(feuillesAAfficher) {
  if (!feuillesAAfficher || !Array.isArray(feuillesAAfficher) || feuillesAAfficher.length === 0) {
    throw new Error("Le paramtre feuillesAAfficher doit tre un tableau non vide");
  }

  try {
    await Excel.run(async (context) => {
      // 1. Charger la liste de toutes les feuilles existantes
      const allSheets = context.workbook.worksheets;
      allSheets.load("items/name");
      await context.sync();
      
      // 2. Filtrer pour ne garder que les feuilles existantes
      const existingSheets = allSheets.items.map(sheet => sheet.name);
      const sheetsToProcess = feuillesAAfficher.filter(name => existingSheets.includes(name));
      
      // 3. Afficher les feuilles concernes
      sheetsToProcess.forEach(sheetName => {
        const sheet = context.workbook.worksheets.getItem(sheetName);
        sheet.visibility = "Visible";
      });
      
      await context.sync();
      
      // 4. Optionnel: logger les feuilles manquantes
      const missingSheets = feuillesAAfficher.filter(name => !existingSheets.includes(name));
      if (missingSheets.length > 0) {
        console.warn("Feuilles non trouves:", missingSheets);
      }
    });
  } catch (error) {
    console.error("Erreur dans unhideSheetsExport:", error);
    throw error;
  }
}

// Fonction pour vrifier si une feuille est visible
async function isSheetVisible(sheetName) {
  try {
    let isVisible = false;
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getItem(sheetName);
      sheet.load('visibility');
      await context.sync();
      isVisible = sheet.visibility === "Visible";
    });
    return isVisible;
  } catch (error) {
    console.error(`Erreur lors de la vrification de la visibilit de ${sheetName}:`, error);
    return false;
  }
}

// Fonction pour masquer toutes les feuilles sauf une spcifique
async function hideAllSheetsExcept(sheetsToKeepVisible) {
  try {
    await Excel.run(async (context) => {
      const sheets = context.workbook.worksheets;
      sheets.load('items/name,items/visibility');
      await context.sync();

      //  Activer une des feuilles visibles pour viter lerreur
      const sheetToActivate = sheets.items.find(sheet => sheetsToKeepVisible.includes(sheet.name));
      if (sheetToActivate) {
        sheetToActivate.activate();
        await context.sync();
      }

      for (const sheet of sheets.items) {
        if (!sheetsToKeepVisible.includes(sheet.name) && sheet.visibility === "Visible") {
          sheet.visibility = "Hidden";
        }
      }
      await context.sync();
    });
  } catch (error) {
    console.error("Erreur lors du masquage des feuilles:", error);
    throw error;
  }
}



// Fonctions masquer/afficher gnralises
function hide(sheetName) {
  Excel.run(async (context) => {
    const sheet = context.workbook.worksheets.getItem(sheetName);
    sheet.visibility = "Hidden";
    await context.sync();
    console.log(`${sheetName} masque`);
  }).catch((error) => {
    console.error("Erreur:", error);
  });
}

function unhide(sheetName) {
  Excel.run(async (context) => {
    const sheet = context.workbook.worksheets.getItem(sheetName);
    sheet.visibility = "Visible";
    await context.sync();
    console.log(`${sheetName} affiche`);
  }).catch((error) => {
    console.error("Erreur:", error);
  });
}


