function setupDataChangeListener() {
    // Initialisation de la surveillance
    Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getItem("ACCUEIL");
        const range = sheet.getRange("B13:H120");
        range.load("values");
        
        await context.sync();
        lastDataSnapshot = JSON.stringify(range.values);
        
        // Surveillance continue
        setInterval(async () => {
            if (notificationCooldown) return;
            
            try {
                await Excel.run(async (context) => {
                    const currentRange = context.workbook.worksheets
                        .getItem("ACCUEIL")
                        .getRange("B13:H120");
                    currentRange.load("values");
                    
                    await context.sync();
                    const currentData = JSON.stringify(currentRange.values);
                    
                    if (currentData !== lastDataSnapshot && !changeDetected) {
                        changeDetected = true;
                        showDataChangeNotification();
                        lastDataSnapshot = currentData;
                    }
                });
            } catch (error) {
                console.log("Erreur de surveillance :", error);
            }
        }, 5000); // Vrification toutes les 5 secondes
    });
}

